  <template>
    <div>
      <div class="about--container">
        <aside class="md:w-1/3">
          <div
              class="border-b md:border-b-0 md:mb-4 md:border-r border-gray-400 border-opacity-75 py-4 md:py-0 md:px-4 mx-4 md:mx-0 md:sticky top-24">
            <div class="profile-picture">
            </div>
            <div class="text-center font-bold uppercase text-3xl md:text-xl">
              <h1 class="mt-4">{{ details['name'][lang] }}</h1>
            </div>
                <div class="p-0 lg:p-4">
                    <h1 class="hidden md:block background text-center rounded">
                        {{ labels['details'][lang] }}
                    </h1>
                    <div class="detail--container">
                        <brief-case-icon/>
                        <span>Web developer</span>
                    </div>
                    <div class="detail--container">
                        <birthday-cake-icon/>
                        <span>2000</span>
                    </div>
                    <div class="detail--container">
                        <location-icon/>
                        <address>{{ details['address'][lang] }}</address>
                    </div>
                    <div class="detail--container">
                        <email-icon/>
                        <a href="mailto:huyducphamofficial@gmail.com">huyducphamofficial@gmail.com</a>
                    </div>
                </div>
            </div>
        </aside>

        <article class="md:w-2/3 p-4">
            <section class="py-4" v-if="timeline.length">
                <h1 :class="['section--title', isTimelineHovered()?'shadow-md bg-gray-200 text-black ':'']">
                    {{labels['titles']['education'][lang]}}
                </h1>
                <div class="mt-4">
                    <div class="flex flex-col md:grid grid-cols-9 mx-auto lg:p-2">
                        <div v-for="(item, index) in timeline"
                            :class="['flex md:contents', index%2?'':'flex-row-reverse justify-end']"
                            :key="item.title">
                            <!-- left -->
                            <div class="col-start-1 about--item--background col-end-5 my-4 cursor-pointer"
                                 v-if="!index%2"
                                @mouseover="hoverHandler(item, true)"
                                @mouseleave="hoverHandler(item, false)">
                                <h3 :class="{'font-semibold text-base mb-1':1, 'underline':item.hover }">
                                    {{ item.title }}
                                </h3>
                                <p class="leading-tight font-serif">
                                    {{ item.content[lang] }}
                                </p>
                            </div>
                            <div class="col-start-5 col-end-6 mr-10 md:mx-auto relative">
                                <div class="h-full w-6 flex items-center justify-center">
                                    <div class="h-full w-1 bg-gray-400 opacity-75 pointer-events-none"/>
                                </div>
                                <div
                                    :class="['transform transition w-6 h-6 absolute top-1/2 -mt-3 rounded-full shadow-md', item.hover?'bg-gray-200 scale-110':'bg-gray-400']"/>
                            </div>
                            <!-- right -->
                            <div
                                v-if="index%2"
                                class="about--item--background col-start-6 col-end-10 my-4 cursor-pointer"
                                @mouseover="hoverHandler(item, true)"
                                @mouseleave="hoverHandler(item, false)">
                                <h3 :class="{'font-semibold text-base mb-1':1, 'underline':item.hover }">
                                    {{ item.title }}
                                </h3>
                                <p class="leading-tight font-serif">
                                    {{ item.content[lang] }}
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

            <section class="py-4">
                <h1 class="section--title">
                    {{labels['titles']['abilities'][lang]}}
                </h1>
                <div class="lg:p-2">
                    <div
                        class="about--item--background flex flex-row-reverse items-center justify-end mt-4">
                        <h3 class="">
                            Website development & maintenance
                        </h3>
                        <div class="pr-4">
                            <Globe class="h-12 fill-current"/>
                        </div>
                    </div>
                    <div
                        class="about--item--background flex flex-row-reverse items-center justify-end mt-4">
                        <h3 class="">
                            Media content editor
                        </h3>
                        <div class="pr-4">
                            <Media class="h-12 fill-current"/>
                        </div>
                    </div>
                </div>
            </section>
            <section class="py-4" v-if="experiences.length">
                <h1 class="section--title">
                    {{ labels['titles']['work_experience'][lang] }}
                </h1>
                <div class="lg:p-2">
                    <div class="about--item--background p-4 mt-4 flex justify-around flex-wrap">
                        <div class="flex flex-col text-center py-4 p-2"
                             v-for="experience in experiences"
                             :key="experience.number+experience.suffix">
                            <CountUp
                                keep-alive="keep-alive"
                                :count-to="experience.number"
                                :suffix="experience.suffix"
                                class="text-3xl xl:text-5xl font-bold"/>
                            <p class="mt-4">
                                {{ experience.title[lang] }}
                            </p>
                        </div>
                    </div>
                </div>
            </section>

            <section class="py-4" v-if="technologies.length">
                <h1 class="section--title">
                    {{labels['titles']['technical_skill'][lang]}}
                </h1>
                <technology-component class="lg:p-2" :technologies="technologies" :lang="lang"/>
            </section>

            <section class="py-4">
                <h1 class="section--title">
                    {{ labels['titles']['media_skill'][lang] }}
                </h1>
                <div class="lg:p-2">
                    <div class="mt-4 px-4">
                        <i>{{ media_skills.description[lang] }}</i>
                    </div>
                    <ul class="background mt-4 justify-around flex p-4 flex-wrap">
                        <li class="mx-4 my-4 h-auto w-20">
                          <img src="/images/icons/photoshop-cc-4.svg" alt="" class="shadow-md cursor-pointer"
                               title="Adobe Photoshop"
                               style="box-shadow: 0 0 50px 0 #31c5f0">
                        </li>
                      <li class="mx-4 my-4 h-auto w-20">
                        <img src="/images/icons/premiere-cc.svg" alt="" class="shadow-md cursor-pointer"
                             title="Adobe Premiere"
                             style="box-shadow: 0 0 50px 0 #e579ff">
                      </li>
                      <li class="mx-4 my-4 h-auto w-20">
                        <img src="/images/icons/after-effects-cc.svg" alt="" class="shadow-md cursor-pointer"
                             title="Adobe After Effect"
                             style="box-shadow: 0 0 50px 0 #d8a1ff">
                      </li>
                    </ul>
                </div>
            </section>
            <section class="py-4">
                <h1 class="section--title">
                    {{labels['titles']['communication'][lang]}}
                </h1>
                <div class=""/>
            </section>
        </article>
    </div>
      <section class="about--container justify-evenly">
          <div class="mt-4 mx-4 md:mr-0 flex-grow basis-0 transition md:w-0">
              <theme-switcher :lang="lang" :labels="theme_mode"/>
          </div>
          <div class="mt-4 mx-4 md:mr-0 flex-grow basis-0 transition md:w-0">
              <div class="cursor-pointer flex items-center about--item--background rounded-lg">
                  <div class="h-8 w-8 overflow-hidden">
                      <download-icon></download-icon>
                  </div>
                  <span class="px-4">
                      {{ labels['save_a_copy'][lang] }}
                  </span>
              </div>
          </div>
          <div class="my-4 mx-4 flex-grow basis-0 transition md:w-0">
              <div class="cursor-pointer flex items-center about--item--background rounded-lg" @click="$emit('changeLanguage')">
                  <div class="h-8 w-8 overflow-hidden">
                      <globe class="fill-current"/>
                  </div>
                  <span class="px-4">
                 <button>{{languages.find((item) => item['key'] == lang)['title']}}</button>
             </span>
              </div>
          </div>
      </section>
      <ModalForm :formData="this.form" :lang="lang" :show-form="modalForm" v-if="isSticky"/>
  </div>
</template>

<script>
import ThemeSwitcher from "./ThemeSwitcher";
// import TechnologyComponent from "./TechnologyComponent";
import Globe from "./icons/Globe";
import Media from "./icons/Media";
import DownloadIcon from "./icons/DownloadIcon";
import BriefCaseIcon from "./icons/BriefCaseIcon";
import BirthdayCakeIcon from "./icons/BirthdayCakeIcon";
import LocationIcon from "./icons/LocationIcon";
import EmailIcon from "./icons/EmailIcon";
//utilities
import CountUp from "./SubComponents/CountUp";
import data from "./lang";

const ModalForm = () => ({
  component: import("./ModalForm"),
});
export default {
  name: "AboutPage",
  components: {
    Globe,
    Media,
    ThemeSwitcher,
    CountUp,
    DownloadIcon,
    BriefCaseIcon,
    BirthdayCakeIcon,
    LocationIcon,
    EmailIcon,
    ModalForm,
    TechnologyComponent: () => import("./TechnologyComponent"),
    //
  },
  created: function () {
  },
  mounted: function () {
    this.$options.containerHeight = document.querySelector(".simplebar-content-wrapper").clientHeight;
  },
  computed: {
    modalForm: function () {
      return this.isSticky + this.$options.containerHeight === document.querySelector(".simplebar-content").clientHeight
    }
  },
  props: ['lang', 'isSticky'],
  emits: ['changeLanguage'],
  data: function () {
    return {
      ...data,
    }
  },
  methods: {
    hoverHandler: function (item, value) {
      item.hover = value;
    },
    isTimelineHovered: function () {
      return this.timeline.find(item => item.hover);
    }
  },
}
</script>

